  background-position: center center;
  background-size: cover;
}

.hero-section::before {
  position: absolute;
  bottom: -20px;
  left: 0;
  z-index: 150;
  width: 100%;
  height: 97px;
  background: linear-gradient(180deg, rgba(213, 191, 244, 0.2) 0%, #d5bff4);
  content: "";
}

.hero-section .layer {
