}

.hero-section .layer._sky3 {
  top: 220px;
  left: 23px;
  width: 78px;
  height: 78px;
  animation: levitateXOut 3s ease-in-out infinite;
}

.hero-section .layer._sky4 {
  top: 380px;
  left: 2px;
  width: 180px;
  height: 180px;
  animation: levitateSmallX 3s ease-in-out infinite;
}

.hero-section .layer._sky5 {
  top: 600px;
  left: 0;
  width: 181px;
  height: 188px;
  animation: levitateSmallX 3s ease-in-out infinite;
  animation-delay: 0.6s;
}

.hero-section .layer._sky6 {
  top: 170px;
  right: 30px;
  width: 132px;
  height: 132px;
  animation: levitateXOut 6s ease-in-out infinite;
}

.hero-section .layer._sky7 {
