  right: 0;
  width: 58px;
  height: 190px;
}

.hero-section .layer._sky8 {
  top: 341px;
  right: 0;
  width: 112px;
  height: 142px;
  animation: levitateSmall 6s ease-in-out infinite;
}

.hero-section .layer._sky9 {
  top: 610px;
  right: 0;
  width: 245px;
  height: 189px;
  animation: levitateSmallXOut 6s ease-in-out infinite;
}

.hero-section .layer._skyTinny {
  bottom: 230px;
  left: 350px;
  width: 185px;
  height: 100px;
  animation: levitateSmallXOut 6s ease-in-out infinite;
}

.hero-section .layer._ball1 {
  top: 267px;
  left: 190px;
  width: 292px;
  height: 654px;
  animation: levitate 6s ease-in-out infinite;
}

.hero-section .layer._ball2 {
  top: 470px;
  left: 450px;
  width: 138px;
  height: 378px;
  animation: levitateOut 6s ease-in-out infinite;
}

.hero-section .layer._ball3 {
  top: 154px;
  right: 660px;
  width: 196px;
  height: 443px;
  animation: levitate 6s ease-in-out infinite;
}

.hero-section .layer._ball4 {
  top: 300px;
  right: 466px;
  width: 183px;
  height: 484px;
  animation: levitateOut 6s ease-in-out infinite;
}

.hero-section .layer._light-right {
  top: 0;
  right: 0;
  z-index: 20;
  width: 817px;
  height: 663px;
}

.hero-section .layer._light-left {
  top: 0;
  left: 0;
  z-index: 50;
  width: 1133px;
  height: 838px;
}

.hero-section .layer._hero-mountains {
  bottom: -70px;
  left: 0;
  z-index: 50;
  width: 100%;
  height: 460px;
  background-position: bottom center;
  background-size: cover;
}

.hero-section .layer._rabbit {
  bottom: 0;
  left: 630px;
  width: 221px;
  height: 549px;
  perspective: 800px;
  animation: levitateOut 6s ease-in-out infinite;
}

.hero-section .layer._rabbit._hover .rabbit-ear-right {
  animation: tickling1 0.5s linear;
}

.hero-section .layer._rabbit._hover .rabbit-ear-left {
  animation: tickling2 0.5s linear;
}

@keyframes tickling1 {
  0% {
    transform: rotate(0) translate(0, 0);
  }
  25% {
    transform: rotate(-8deg) translate(-3px, 0);
  }
  75% {
    transform: rotate(8deg) translate(5px, 4px);
  }
  100% {
    transform: rotate(0) translate(0, 0);
  }
}

@keyframes tickling2 {
  0% {
    transform: rotate(0) translate(0, 0);
  }
  25% {
    transform: rotate(-8deg) translate(-4px, -2px);
  }
  75% {
    transform: rotate(8deg) translate(4px, 3px);
  }
  100% {
    transform: rotate(0) translate(0, 0);
  }
}

.hero-section .layer._rabbit .rabbit-component {
  position: absolute;
  top: 0;
  left: 0;
  width: 221px;
  height: 549px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.hero-section .layer._rabbit .rabbit {
  z-index: 15;
}

.hero-section .layer._rabbit .rabbit-ear-left {
  top: -1.7px;
  right: 30px;
  left: auto;
  z-index: 16;
  width: 48px;
  height: 66px;
  animation: ear-left 6s ease-in-out infinite;
}

.hero-section .layer._rabbit .rabbit-ear-right {
  top: 20px;
  right: 1px;
  left: auto;
  z-index: 16;
  width: 47px;
  height: 72px;
  animation: ear-right 6s ease-in-out infinite;
}

.hero-section .layer._rabbit .rabbit-leg {
  top: 85px;
  left: 92px;
  z-index: 17;
  width: 38px;
  height: 63px;
  animation: rabbit-leg 6s ease-in-out infinite;
}

@keyframes rabbit-leg {
  0% {
    transform: rotate(0) translate(0, 0);
  }
  50% {
    transform: rotate(-21deg) translate(12px, 3px);
  }
  100% {
    transform: rotate(0) translate(0, 0);
  }
}

@keyframes ear-left {
  0% {
    transform: rotate(0) translateX(0) translateY(0) skewY(0);
  }
  50% {
    transform: rotate(-12deg) translateX(-4px) translateY(-3px) skewY(-9deg);
  }
  100% {
    transform: rotate(0) translateX(0) translateY(0) skewY(0);
  }
}

@keyframes ear-right {
  0% {
    transform: rotate(0) translateX(0) translateY(0) skewY(0);
  }
  50% {
    transform: rotate(13deg) translateX(5px) translateY(-3px) skewY(-12deg);
  }
  100% {
    transform: rotate(0) translateX(0) translateY(0) skewY(0);
  }
}

.hero-section .layer._rabbit-small {
  right: 140px;
